@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap');
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}

body {
  background-color: white;
  position: relative;
  min-height: 100vh;
  /* background-image: linear-gradient(white, #2f90eb); */
}

.theme-color {
  background-color: rgb(15, 83, 166);
}

.content-wrap {
  padding-bottom: 4rem; 
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 4rem;
}

.navbar-link {
  color: white;
  font-weight: 700;
  text-decoration: none;
}

.navbar-link:hover{
  text-decoration: none;
}

.logo {
  border-style: none;
  text-decoration: none;
}

.logo:hover{
  text-decoration: none;
  text-shadow: 2px 2px #333;
  color: white;
}

.highlighted-btn {
  background-color: transparent;
  border: white 2px solid;
  color: white;
  width: 100%;
}

.panel-nav-btn{
  background-color: transparent;
  color: black;
  border: none;
  padding: 10px;
  font-size: large;
  width: 100%;
}

.move-task-btn{
  width: 100%;
  font-size:xx-large;
  padding: 5px;
}

